import { Injectable } from '@angular/core';
import { Headers, Http, Response, URLSearchParams } from '@angular/http';

import { Context } from '../context';
import { WebService } from '../webapi';
import {CatalogInfo, CatalogService} from '../entities';
import {SessionInfo} from '../security/session';

///////////////////// MODELS //////////////////////////////////////////////////
export class PaisInfo extends CatalogInfo {
    nombre: string;
}

export class EstadoInfo extends CatalogInfo {
    pais: PaisInfo;
    nombre: string;
}

export class MunicipioInfo extends CatalogInfo {
    estado: EstadoInfo;
    nombre: string;
}

export class PoblacionInfo extends CatalogInfo {
    municipio: MunicipioInfo;
    nombre: string;
}

export class AsentamientoHumanoInfo extends CatalogInfo {
    poblacion: PoblacionInfo;
    nombre: string;
    codigoPostal: string
    idTipoAsentamiento: number;
    tipoAsentamiento: string;
}

export class DomicilioInfo {
    calle: string;
    numeroExterior: string;
    numeroInterior: string;
    cruce1: string;
    cruce2: string;    
    colonia: string;
    codigoPostal: string;
    poblacion: PoblacionInfo;

    asentamientoHumano: AsentamientoHumanoInfo;
}


///////////////////// SERVICES //////////////////////////////////////////////////
@Injectable()
export class PoblacionService extends CatalogService<PoblacionInfo> {

    constructor(http: Http, context: Context) {
        super(http, context, "api/kernel/general/poblaciones");
    }

    create(): Promise<PoblacionInfo> {
        return Promise.resolve(new PoblacionInfo());
    }


}